/* Set background color for the entire page */
html, body {
  @apply bg-black;
  color: white;
}

/* Additional styling for header */
header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .text-2xl {
  margin-left: 1rem;

  font-family: Lato, arial, sans-serif;
}

header ul {
  display: flex;
  justify-content: center;
  align-items: center;
}

header li {
  list-style: none;
}

header a {
  text-decoration: none;
  color: white;
  padding: 0.5rem 1rem;

  font-family: Lato, arial, sans-serif;
}

header a:hover {
  text-decoration: underline;
  
}

