.space-y-6 > :not([hidden]) ~ :not([hidden]) {
  --tw-space-y-reverse: 0;
  margin-top: calc(1.5rem * calc(1 - var(--tw-space-y-reverse)));
  margin-bottom: calc(1.5rem * var(--tw-space-y-reverse));
}

/* Styles for the slider images */
.slider-img {
  width: 100%;
  height: 200px; /* Default height for mobile screens */
  object-fit: cover; /* Ensure the image covers the container while maintaining aspect ratio */
}

@media (min-width: 640px) {
  .slider-img {
    height: 300px; /* Adjust height for larger screens */
  }
}

@media (min-width: 1024px) {
  .slider-img {
    height: 400px; /* Adjust height for even larger screens */
  }
}





/* Custom styles for the Services section */
#services .text-center h2 {
  @apply text-4xl font-bold mb-2;
}

#services .text-center p {
  @apply text-lg;
}

#services .grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 2rem;
}

@media (min-width: 640px) {
  #services .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 768px) {
  #services .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1024px) {
  #services .grid {
    grid-template-columns: repeat(6, 1fr);
  }
}

/* Styles for the service items */
#services .service-item {
  position: relative;
  padding: 1rem;
  background-color: #2d3748; /* bg-gray-800 */
  border-radius: 0.5rem; /* rounded-lg */
  text-align: center;
}

#services .service-item .absolute {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s;
}

#services .service-item:hover .absolute {
  opacity: 1;
}

#services .service-item .icon {
  margin-bottom: 0.5rem; /* mb-2 */
  color: rgb(8, 172, 194); /* text-white */
  font-family: Lato, arial, sans-serif;
}

#services .service-item h4 {
  margin-bottom: 0.5rem; /* mb-2 */
  color: white;
  font-size: 0.875rem; /* text-sm */
  font-weight: 700; /* font-bold */
  font-family: Lato, arial, sans-serif;
}

#services .service-item .absolute button {
  color:rgb(145, 145, 145);
  font-size: 0.75rem; /* text-xs */
  border: 1px solid white; /* border border-white */
  border-radius: 0.25rem; /* rounded */
  padding: 0.25rem; /* p-1 */
  transition: transform 0.3s; /* transition-transform */
  font-family: Lato, arial, sans-serif;
}

#services .service-item .absolute button:hover {
  transform: scale(1.1); /* hover:scale-110 */
}

/* Styles for the additional service items */
#services .additional-service-item {
  padding: 1rem; /* p-4 */
  background-color: #2d3748; /* bg-gray-800 */
  border-radius: 0.5rem; /* rounded-lg */
  text-align: center;
  font-family: Lato, arial, sans-serif;
}

#services .additional-service-item h4 {
  margin-bottom: 0.5rem; /* mb-2 */
  color: white; /* text-xl */
  font-weight: 700; /* font-bold */
  font-family: Lato, arial, sans-serif;
  
}

#services .additional-service-item p {
  margin-bottom: 0.5rem; /* mb-2 */
  color:rgb(145, 145, 145);
  font-family: Lato, arial, sans-serif;
}

#services .additional-service-item a {
  color: #00245f; /* text-blue-500 */
 
  border: 1px solid white; /* border border-white */
  border-radius: 0.25rem; /* rounded */
  padding: 0.25rem 0.75rem; /* py-1 px-3 */
  display: inline-block;
}





/* Custom styles for the Contact section */
#contact .container {
  max-width: 600px;
  padding: 1rem;
  background-color: #2d3748;
  border-radius: 0.5rem;
  color: rgb(145, 145, 145);
  font-family: 'Lato', arial, sans-serif;
}

#contact form {
  margin-top: 1rem;
}

#contact form label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.25rem;
}

#contact form input,
#contact form select,
#contact form textarea {
  width: 100%;
  padding: 0.25rem;
  border: 1px solid #4a5568;
  border-radius: 0.375rem;
  background-color: #2d3748;
  color: rgb(145, 145, 145);
  font-family: 'Lato', arial, sans-serif;
}

#contact form select {
  appearance: none;
}

#contact form button {
  background-color: #3b82f6;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  transition: background-color 0.3s;
}

#contact form button:hover {
  background-color: #1e40af;
}

#contact .mt-4 {
  margin-top: 1rem;
}

@media (max-width: 640px) {
  #contact .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
  }
}

@media (min-width: 641px) and (max-width: 768px) {
  #contact .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
  }
}

@media (min-width: 769px) {
  #contact .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0.5rem;
  }
}

/* Custom styles for the submission confirmation */
#contact .confirmation-box {
  background-color: #2d3748;
  padding: 1rem;
  border-radius: 0.375rem;
  margin-top: 1rem;
}

#contact .confirmation-box h3 {
  font-size: 1.125rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

#contact .confirmation-box p {
  margin-bottom: 0.5rem;
}

/* Custom styles for the Company section */
#company {
  background-color: #2d3748;
  color: rgb(145, 145, 145);
  font-family: 'Lato', arial, sans-serif;
  padding: 2rem;
  border-radius: 0.5rem;
  margin-top: 2rem;
  animation: fadeIn 1s ease-in-out;
}

#company .content {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

#company .content-left {
  flex: 2;
}

#company .content-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

#company .content-right img {
  width: 100%;
  border-radius: 0.375rem;
}

@media (max-width: 768px) {
  #company .content {
    flex-direction: column;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#company .text-animation {
  animation: slideIn 1s ease-in-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

